* {
  box-sizing: border-box;
}

body {
  font-family: $poppins;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  color: $basic-black;
  background-color: $white;
}

.body-position {
  position: fixed;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}
